// @ts-nocheck
import { PayloadAction } from '@reduxjs/toolkit';
import { createSlice } from 'utils/@reduxjs/toolkit';
import { useInjectReducer, useInjectSaga } from 'utils/redux-injectors';

import { CalendarState } from './types';

export const initialState: CalendarState = {
  calendarDeviceSerial: [],
  totalPage: 1,
  currentPage: 0,
};

const getFilterData = payload => {
  // console.log(payload);
  if (payload.type === 'platform') {
    const filterData = payload.data.filter(
      item => item.platform === payload.key,
    );
    //console.log(filterData);
    return filterData.map(item => item.serial);
  }
  if (payload.type === 'search') {
    const filterData = payload.data.filter(
      item =>
        item.platform.toLowerCase() === payload.key ||
        item.brand.toLowerCase() == payload.key ||
        item.serial.toLowerCase() == payload.key ||
        item.modelName.toLowerCase() == payload.key ||
        item.deviceParameters.city.toLowerCase() == payload.key,
    );
    //console.log(filterData);
    return filterData.map(item => item.serial);
  }

  return [];
};

const slice = createSlice({
  name: 'Calendar',
  initialState,
  reducers: {
    loadDeviceSerial: (state, action: PayloadAction<any>) => {
      //console.log(action.payload)
      state.calendarDeviceSerial = action.payload.map(item => item.serial);
      state.totalPages = Math.ceil(action.payload.length / 10);
    },
    loadFilterDeviceSerial: (state, action: PayloadAction<any>) => {
      state.calendarDeviceSerial = getFilterData(action.payload);
      state.totalPages = Math.ceil(action.payload.data.length / 10);
    },
    loadFilterDeviceForTray: (state, action: PayloadAction<any>) => {
      state.calendarDeviceSerial = action.payload;
    },
  },
});

export const { actions: CalendarActions } = slice;

export const useCalendarSlice = () => {
  useInjectReducer({ key: slice.name, reducer: slice.reducer });
  //useInjectSaga({ key: slice.name, saga: HomePageSaga });
  return { CalendarActions: slice.actions };
};
