// @ts-nocheck
import { Flex, Icon } from '@chakra-ui/react';
import ButtonComp from 'app/components/ButtonComp';
import SearchBar from 'app/pages/TestAnalytics/components/SearchBar';
import React from 'react';
import { BiSearch } from 'react-icons/bi';

function SearchbarWithButton({
  searchItem,
  setSearchItem,
  searchItemsHandler,
}) {
  const enterKeyHandler = event => {
    if (event.keyCode === 13) {
      searchItemsHandler();
    }
  };

  return (
    <Flex paddingRight="20px" width="40%" align="center">
      <SearchBar
        width="70%"
        enterKeyHandler={enterKeyHandler}
        searchItem={searchItem}
        setSearchItem={setSearchItem}
      ></SearchBar>
      <ButtonComp
        marginLeft="0px"
        padding="9px  10px"
        name="search-testanalytic-key-button"
        buttonHandler={searchItemsHandler}
      >
        <Icon as={BiSearch}></Icon>
      </ButtonComp>
    </Flex>
  );
}

export default SearchbarWithButton;
