// @ts-nocheck
import { Select } from '@chakra-ui/react';
import React from 'react';

function SelectTrayOption({
  title,
  selectValue,
  setSelectValue,
  selectOptions,
  type,
  width,
}) {
  return (
    <Select
      placeholder={title}
      size="sm"
      width={width || '230px'}
      color="grey13"
      h="34px"
      border="1px solid "
      borderColor="grey1"
      borderRadius="5px"
      data-select={title}
      value={selectValue}
      onChange={e => setSelectValue(e.target.value)}
      _focus={{ boxShadow: ' 0 0 0 white', borderColor: 'grey1' }}
    >
      {selectOptions &&
        selectOptions.map((item, index) => (
          <option
            data-select-project={
              type === 'project' && item.trayName + '_' + index
            }
            key={index}
            value={type === 'project' ? item.name : index}
          >
            {type === 'project' ? item.name : item.trayName}
          </option>
        ))}
    </Select>
  );
}

export default SelectTrayOption;
