// @ts-nocheck
import {
  Box,
  Center,
  Flex,
  IconButton,
  useRadio,
  Icon,
} from '@chakra-ui/react';
import React from 'react';
import { AiFillAndroid } from 'react-icons/ai';
import { FaApple } from 'react-icons/fa';
import { RiRefreshLine } from 'react-icons/ri';
import { VscClearAll } from 'react-icons/vsc';

function CustomIconButton(props) {
  const { getInputProps, getCheckboxProps } = useRadio(props);

  const input = getInputProps();
  const checkbox = getCheckboxProps();

  //console.log(checkbox);
  // console.log(input);

  return (
    <Box as="label">
      <input {...input} />
      {/* <IconButton
         {...checkbox}
        aria-label="androidIcon"
        bg="white"
        borderRadius="3px"
        w="39px"
        h="39px"
        color="grey5"
        border=".2px solid #C8C8C8"
        _focus={{ boxShadow: 'none' }}
        _checked={{ bg: 'white3', color: 'blue1' }}
        icon={   props.children === "ios" ?  <FaApple /> : <AiFillAndroid />  }
      /> */}
      <Center
        align="center"
        justify="center"
        {...checkbox}
        cursor="pointer"
        bg={props.children && 'white'}
        border={props.children && '.2px solid #C8C8C8'}
        borderRadius="3px"
        w={props.width || '39px'}
        h={props.height || '39px'}
        color="grey5"
        _checked={
          props.children && {
            bg: 'white3',
            color: 'blue1',
            border: '1px solid #F3F6F9',
          }
        }
      >
        {props.children === 'ios' ? (
          <Icon title="ios" width="16px" as={FaApple} height="18px"></Icon>
        ) : (
          <>
            {props.children === 'android' ? (
              <Icon
                title="android"
                width="16px"
                as={AiFillAndroid}
                height="20px"
              ></Icon>
            ) : (
              <Icon
                title="clear"
                width="16px"
                as={VscClearAll}
                height="20px"
              ></Icon>
            )}
          </>
        )}
      </Center>
    </Box>
  );
}

export default CustomIconButton;
