import { createSelector } from '@reduxjs/toolkit';
import { initialState } from '.';

import { RootState } from 'types';

// First select the relevant part from the state
const selectSlice = (state: RootState) => state.myDevices || initialState;

export const selectMyDevicesData = createSelector(
  [selectSlice],
  state => state.myDevicesData,
);

