// @ts-nocheck
import { Flex, Tooltip } from '@chakra-ui/react';
import React, { useState } from 'react';
import FullCalendar from '@fullcalendar/react'; // must go before plugins
import resourceTimeline from '@fullcalendar/resource-timeline'; // a plugin!
import timeGridFourDay from '@fullcalendar/timegrid';
import './tooltip.css';
function FullCalendarComp({ eventData, resourseData }) {
  const [calendarTooltip, setCalendarTooltip] = useState('');
  const wrapperElement = document.createElement('div');
  const [slotDuration, setSlotDuration] = useState(10);
  const handleEventMouseEnter = info => {
    // setCalendarTooltip(info.event.title);
    const elementToWrap = info.el;
    //elementToWrap.style.position = 'relative';

    //  elementToWrap.title = info.event.extendedProps.description;

    // console.log(info.event.title);
    //console.log(info.event.start);
    // console.log(info.event.end);
    // console.log(info.event.extendedProps.description);
    //wrapperElement.className = 'wrapper-tooltip'
    elementToWrap.title = `${new Date(
      info.event.start,
    ).toLocaleTimeString()} - ${new Date(
      info.event.end,
    ).toLocaleTimeString()}, Project Name : ${
      info.event.extendedProps.projectName
    }`;

    // elementToWrap.title = titleElement.outerHTML;

    //console.log(elementToWrap)
    //elementToWrap.appendChild(wrapperElement);
    //setCalendarTooltip(elementToWrap);
    //console.log(wrapperElement)
  };

  const handleEventMouseLeave = () => {
    // wrapperElement.className = '';
    //wrapperElement.innerText ='title'
    //calendarTooltip.removeChild(wrapperElement);
    setCalendarTooltip('');
    // wrapperElement.innerText = 'tooltip'
  };

  const handleDurationChange = duration => {
    setSlotDuration(duration);
  };

  return (
    <Flex>
      <FullCalendar
        style={{
          fontSize: '12px',
          fontWeight: '500',
          fontFamily: 'Montserrat',
        }}
        schedulerLicenseKey="CC-Attribution-NonCommercial-NoDerivatives"
        plugins={[resourceTimeline, timeGridFourDay]}
        initialView="timeGridFourDay"
        // initialDate="2021-05-23"
        nowIndicator={true}
        locale="en"
        contentHeight="auto"
        timeFormat="hh:mm" // uppercase H for 24-hour clockã€
        dayHeaderFormat={{
          weekday: 'short',
          month: 'numeric',
          day: 'numeric',
          year: 'numeric',
        }}
        displayEventTime={false}
        //  displayEventTooltip={true}
        columnFormat="ddd"
        scrollTime="0"
        slotDuration={{ minute: slotDuration }}
        slotLabelFormat={[
          {
            year: 'numeric',
            month: 'long',
            day: 'numeric',
            meridiem: 'short',
          }, // top level of text
          {
            hour: '2-digit',
            minute: '2-digit',
            meridiem: true,
            hour12: true,
          }, // lower level of text
        ]}
        firstDay="1"
        views={{
          timeGridFourDay: {
            type: 'resourceTimeline',
            slotDuration: `00:${slotDuration}`,
            style: { fontWeight: '500' },
            displayEventTime: false,
            duration: { days: 1 },
            headerToolbar: false,
            slotLabelFormat: [
              {
                year: 'numeric',
                month: 'long',
                day: 'numeric',
                meridiem: 'short',
              }, // top level of text
              {
                hour: '2-digit',
                minute: '2-digit',
                meridiem: true,
                hour12: true,
              }, // lower level of text
            ],
          },
          displayEventTime: false,
        }}
        headerToolbar={{
          left: 'today,resourceTimelineDay,resourceTimelineWeek, duration5,duration10,duration15',
          center: 'title',
          right: 'prev,next',
        }}
        customButtons={{
          duration5: {
            text: '5',
            click: () => handleDurationChange('05'),
          },
          duration10: {
            text: '10',
            click: () => handleDurationChange('10'),
          },
          duration15: {
            text: '15',
            click: () => handleDurationChange('15'),
          },
        }}
        buttonText={{
          prev: ' < ',
          next: ' > ',
          week: 'week',
        }}
        aspectRatio={1.5}
        resourceAreaHeaderContent="Devices"
        resourceOrder={[
          {
            labelText: 'Device',
            field: 'title',
          },
          {
            labelText: 'Occupancy',
            field: 'occupancy',
          },
        ]}
        resources={resourseData}
        events={eventData}
        eventMouseEnter={handleEventMouseEnter}
        eventMouseLeave={handleEventMouseLeave}
      />
    </Flex>
  );
}

export default FullCalendarComp;
