// @ts-nocheck
import {
  Flex,
  HStack,
  Icon,
  Select,
  Text,
  useRadioGroup,
} from '@chakra-ui/react';
import ButtonComp from 'app/components/ButtonComp';
import CustomIconButton from 'app/pages/Appium/components/CustomIconButton';
import { useMyDevicesSlice } from 'app/pages/MyDevices/slice';
import { selecteTrayList } from 'app/pages/MyDevices/slice/selector';
import SelectTrayOption from 'app/pages/UiAutomator/components/SelectTrayOption';
import { selectListingDevices } from 'app/pages/Xcui/slice/selector';
import React, { useEffect, useState } from 'react';
import { RiRefreshLine } from 'react-icons/ri';
import { useDispatch, useSelector } from 'react-redux';
import { useCalendarSlice } from '../slice';
import SearchbarWithButton from './SearchbarWithButton';
import usePlatformtypeHook from 'app/pages/HomePage/usePlatformtypeHook';

function FilterSearchComponet() {
  const [selectCategory, setSelectCategory] = useState('');
  const [searchItem, setSearchItem] = useState('');
  const options = ['android', 'ios', 'reset'];
  const [selectTrayValue, setTraySelectValue] = useState(-1);
  const getListingDevices = useSelector(selectListingDevices);
  const dispatch = useDispatch();
  const selectTrayOptionList = useSelector(selecteTrayList);
  const { CalendarActions } = useCalendarSlice();
  const { actions } = useMyDevicesSlice();
  const isMobileTesting = usePlatformtypeHook();
  const { getRootProps, getRadioProps } = useRadioGroup({
    name: 'category',
    defaultValue: 'reset',
    onChange: val => setSelectCategory(val),
  });
  const group = getRootProps();
  useEffect(() => {
    dispatch(actions.loadDeviceTrayList(isMobileTesting ? 'app-automation' : 'lr-automation'));
  }, []);
  const applyResetFilterSearchHandler = () => {
    if (getListingDevices.length > 0) {
      dispatch(CalendarActions.loadDeviceSerial(getListingDevices));
      setSearchItem('');
      setSelectCategory('');
      setTraySelectValue(-1);
    }
  };

  useEffect(() => {
    if (parseInt(selectTrayValue) === 0 || parseInt(selectTrayValue) > 0) {
      if (selectTrayOptionList[selectTrayValue].devices.length > 0) {
        dispatch(
          CalendarActions.loadFilterDeviceForTray(
            selectTrayOptionList[selectTrayValue].devices,
          ),
        );
        setSearchItem('');
      }
    } else if (parseInt(selectTrayValue) < 0) {
      //console.log('reset')
    } else {
      if (getListingDevices.length > 0) {
        dispatch(CalendarActions.loadDeviceSerial(getListingDevices));
        setSearchItem('');
      }
    }
  }, [selectTrayValue]);

  //console.log(selectTrayOptionList)
  useEffect(() => {
    if (
      selectCategory !== '' &&
      selectCategory !== null &&
      getListingDevices.length > 0
    ) {
      dispatch(
        CalendarActions.loadFilterDeviceSerial({
          type: 'platform',
          key: selectCategory,
          data: getListingDevices,
        }),
      );
      setTraySelectValue(-1);
      setSearchItem('');
    }
    if (selectCategory === 'reset') {
      applyResetFilterSearchHandler();
    }
  }, [selectCategory]);

  const searchButtonHandler = () => {
    if (searchItem !== '') {
      dispatch(
        CalendarActions.loadFilterDeviceSerial({
          type: 'search',
          key: searchItem.trim().toLocaleLowerCase(),
          data: getListingDevices,
        }),
      );
      setTraySelectValue(-1);
    }
  };

  //console.log(selectTrayValue);

  return (
    <Flex
      justifyContent="space-between"
      alignItems="center"
      alignContent="center"
      padding="10px"
      paddingBottom="0px"
      fontFamily="Montserrat"
      width="100%"
    >
      <SearchbarWithButton
        setSearchItem={setSearchItem}
        searchItem={searchItem}
        searchItemsHandler={searchButtonHandler}
      ></SearchbarWithButton>
      <Flex alignItems="center" alignContent="center" height="40px">
        <HStack {...group} spacing="0px">
          {options.map(value => {
            const radio = getRadioProps({ value });
            return (
              <CustomIconButton
                height="33px"
                width="40px"
                key={value}
                {...radio}
              >
                {value}
              </CustomIconButton>
            );
          })}
        </HStack>
        {/* <Flex
          w="200px"
          //justifyContent="space-between"
          color="grey"
          fontWeight="500"
          alignItems="center"
          alignSelf="end"
          padding="0px 10px"
          alignItems="center"
          alignContent="center"
        >
          <Text marginRight="10px" color="grey13">
            Tray
          </Text>
          <Select
            align="right"
            width="140px"
            h="35px"
            placeholder="select tray"
          >
            <option value="option1">Option 1</option>
            <option value="option2">Option 2</option>
            <option value="option3">Option 3</option>
          </Select>{' '}
        </Flex> */}
        <HStack margin="0px 10px">
          <Text>Tray</Text>
          <SelectTrayOption
            width="140px"
            selectOptions={selectTrayOptionList}
            title="Select Tray"
            selectValue={selectTrayValue}
            setSelectValue={setTraySelectValue}
          ></SelectTrayOption>
        </HStack>

        <ButtonComp
          padding="4px 8px"
          backgroundColor="white"
          color="#3F4254"
          logo={<Icon as={RiRefreshLine} fontWeight="600"></Icon>}
          buttonHandler={applyResetFilterSearchHandler}
        >
          Reset
        </ButtonComp>
      </Flex>
    </Flex>
  );
}

export default FilterSearchComponet;
