// @ts-nocheck
import { Flex, Text } from '@chakra-ui/react';
import LoadingComponent from 'app/components/LoadingComponent';
import NoContent from 'app/components/NoContent';
import { useTestExecutionSlice } from 'app/pages/Xcui/slice';
import {
  selectBusyDeviceLoading,
  selectBusyDeviceSlots,
} from 'app/pages/Xcui/slice/selector';

import React, { useCallback, useEffect, useMemo, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import FullCalendarComp from '../Components/FullCalendarComp';
import {
  selectCalendarDeviceSerial,
  selectTotalPages,
} from '../slice/selector';
const getColorForTag = tagName => {
  // Convert tag name to a numerical hash value
  if(!tagName){
    return '#FF0000';
    }
  let hash = 0;
  for (let i = 0; i < tagName.length; i++) {
    hash = tagName.charCodeAt(i) + ((hash << 5) - hash);
  }
  // Generate RGB values from hash value
  let r = (hash & 0xff0000) >> 16;
  let g = (hash & 0x00ff00) >> 8;
  let b = hash & 0x0000ff;
  // Return color string in hex format (#RRGGBB)
  return '#' + ((1 << 24) + (r << 16) + (g << 8) + b).toString(16).slice(1);
};
const getSchedulerData = list => {
  let eventData = [];
  let resourseData = [];
  for (let i = 0; i < list.length; i++) {
    resourseData.push({
      id: list[i].deviceSerial,
      title: list[i].modelName + ' - ' + list[i].city,
    });

    for (let j = 0; j < list[i].busySlots.length; j++) {

      if(list[i].busySlots[j].busySlotParameters.busyBy !== 'free'){
        // console.log(list[i].busySlots[j].busySlotParameters.busyBy)
        eventData.push({
          start: list[i].busySlots[j].startTime,
          end: list[i].busySlots[j].endTime,
          title: '', // list[i].busySlots[j].busySlotParameters.projectName,
          description: 'description',
          color: getColorForTag(
            list[i].busySlots[j].busySlotParameters.projectName,
          ),
          resourceId: list[i].deviceSerial,
          projectName: list[i].busySlots[j].busySlotParameters.hasOwnProperty(
            'projectName',
          )
            ? list[i].busySlots[j].busySlotParameters.projectName
            : '-',
        });
  
      }
   
    }
  }
  return { eventData, resourseData };
};

function SchedulerComp({ getListingDevices }) {
  //sconsole.log('child render scheduler');
  const getSerialDevices = useSelector(selectCalendarDeviceSerial);
  const dispatch = useDispatch();
  const busyDeviceSlots = useSelector(selectBusyDeviceSlots);
  const loadingBusyDeviceList = useSelector(selectBusyDeviceLoading);
  const [getEventData, setEventData] = useState([]);
  const [getResourseData, setResourseData] = useState([]);
  //const { eventData, resourseData } = getSchedulerData(busyDeviceSlots);
  const { testExecutionActions } = useTestExecutionSlice();
  //const totalPage = useSelector(selectTotalPages);
  const [totalPage, setTotalPage] = useState(0);
  const [currentPage, setCurrentPage] = useState(0);

  useEffect(() => {
    if (totalPage > 0 && getSerialDevices.length > 0) {
      dispatch(
        testExecutionActions.loadindBusyDeviceSlots(
          getSerialDevices.slice(currentPage * 10, currentPage * 10 + 10),
        ),
      );
    }
  }, [currentPage, getSerialDevices]);

  useEffect(() => {
    if (getSerialDevices.length > 0) {
      setTotalPage(Math.ceil(getSerialDevices.length / 10));
    }
  }, [getSerialDevices]);

  useEffect(() => {
    if (busyDeviceSlots.length > 0) {
      const { eventData, resourseData } = getSchedulerData(busyDeviceSlots);
      setEventData(eventData);
      setResourseData(resourseData);
      //console.log(eventData, resourseData)
    }
  }, [busyDeviceSlots]);

  //console.log(busyDeviceSlots)
  const prePageHandler = () => {
    if (currentPage > 0) {
      setCurrentPage(state => state - 1);
    }
  };
  const nextPageHandler = () => {
    if (currentPage + 1 < totalPage) {
      setCurrentPage(state => state + 1);
    }
  };

  //console.log(getSerialDevices);
  return (
    <Flex
      flexDirection="column"
      fontFamily="Montserrat"
      fontWieght="500"
      fontSize="14px"
    >
      {loadingBusyDeviceList ? (
        <LoadingComponent height="80vh"></LoadingComponent>
      ) : (
        <>
          {getSerialDevices.length > 0 ? (
            <>
              <FullCalendarComp
                eventData={getEventData}
                resourseData={getResourseData}
              ></FullCalendarComp>
              <Flex width="100%" justifyContent="flex-end">
                <Flex
                  fontFamily="Montserrat"
                  justifySelf="flex-end"
                  fontWeight="500"
                  className="pagination"
                  boxShadow="0px 3px 6px #00000029"
                  borderRadius="13"
                  marginTop="10px"
                  marginRight="5px "
                  color="grey2"
                  padding="5px 10px"
                  display="inline-block"
                  flexDirection="row"
                >
                  <Text
                    cursor={currentPage <= 0 ? '' : 'pointer'}
                    as="span"
                    padding="0px 5px"
                    disabled={currentPage <= 0 ? true : false}
                    onClick={prePageHandler}
                  >
                    {' '}
                    &lt;{' '}
                  </Text>
                  <Text as="span" textTransform="none" padding="0px 5px">
                    {currentPage + 1} Of {totalPage}
                  </Text>
                  <Text
                    cursor={currentPage + 1 >= totalPage ? '' : 'pointer'}
                    as="span"
                    padding="0px 5px"
                    disabled={currentPage + 1 >= totalPage ? true : false}
                    onClick={nextPageHandler}
                  >
                    {' '}
                    &gt;{' '}
                  </Text>
                </Flex>
              </Flex>
            </>
          ) : (
            <NoContent message="no data found"></NoContent>
          )}
        </>
      )}
    </Flex>
  );
}

export default SchedulerComp;
