//@ts-nocheck
import React, { useEffect } from 'react';
import { Flex, Text } from '@chakra-ui/layout';
import Schedulers from './Components/Schedulers';
import SubContainer from 'app/components/SubContainer';
import SubLayout from 'app/components/SubLayout';
import { useDispatch } from 'react-redux';
import { useTestExecutionSlice } from 'app/pages/Xcui/slice';
import { useSelector } from 'react-redux';
import {
  selectBusyDeviceLoading,
  selectBusyDeviceSlots,
  selectDeviceListLoading,
} from '../Xcui/slice/selector';
import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import { useState } from 'react';
import { selectListingDevices } from 'app/pages/Xcui/slice/selector';
import SchedulerComp from './Features/SchedulerComp';
import Subheader from 'app/components/Subheader';
import LoadingComponent from 'app/components/LoadingComponent';
import NoContent from 'app/components/NoContent';
import FilterSearchComponet from './Components/FilterSearchComponet';
import { useCalendarSlice } from './slice';
import { selectCalendarDeviceSerial } from './slice/selector';
import usePlatformtypeHook from '../HomePage/usePlatformtypeHook';

export function Calender() {
  const dispatch = useDispatch();
  const { testExecutionActions } = useTestExecutionSlice();
  const { CalendarActions } = useCalendarSlice();
  //const busyDeviceSlots = useSelector(selectBusyDeviceSlots);
  const loadingDeviceList = useSelector(selectDeviceListLoading);
  const isMobileTesting = usePlatformtypeHook();
  const getListingDevices = useSelector(selectListingDevices);
  // getListingDevices.map(item => item.serial);

  useEffect(() => {
    dispatch(testExecutionActions.loadDeviceList({testType : isMobileTesting ? 'app-automation,mobile-interact' : 'lr-automation,lr-interact' , platform: ''}))
  }, []);
  useEffect(() => {
    if (getListingDevices.length > 0) {
      //console.log(getListingDevices )
      dispatch(CalendarActions.loadDeviceSerial(getListingDevices));
    }
  }, [getListingDevices]);

  //console.log('parent');

  return (
    <Flex
      flexDirection="column"
      borderRadius="5px"
      bg="white"
      minHeight="100vh"
      width="100%"
    >
      <FilterSearchComponet></FilterSearchComponet>
      <>
        {loadingDeviceList ? (
          <LoadingComponent height="90vh"></LoadingComponent>
        ) : (
          <>
            {getListingDevices.length > 0 ? (
              <Flex
                width="100%"
                //borderRadius="10px"
                //   bg="white"
                //  justifyContent="center"
                padding="15px"
                alignItems="center"
                flexDirection="column"
                fontFamily="Montserrat"
                // margin="10px"
              >
                <>
                  {getListingDevices.length > 0 ? (
                    <SchedulerComp
                      getListingDevices={getListingDevices}
                    ></SchedulerComp>
                  ) : (
                    <NoContent
                      message="No Data Found"
                      height="80vh"
                    ></NoContent>
                  )}
                </>
              </Flex>
            ) : (
              <NoContent message="No Data" height="80vh"></NoContent>
            )}
          </>
        )}
      </>
    </Flex>
  );
}
export default Calender;
